import { siteData } from '@/data/siteData';
import { AnimatedHero } from '@/components/AnimatedHero';
import { BentoGrid } from '@/components/BentoGrid';
import { ReviewsSlider } from '@/components/ReviewsSlider';
import { AboutPreview } from '@/components/AboutPreview';
import { Button } from '@/components/ui/Button';

export default async function Home({
  params,
}: {
  params: Promise<{ locale: string }>;
}) {
  const { locale } = await params;
  const t = await import(`../../../messages/${locale}.json`);

  return (
    <main className="flex flex-col min-h-screen bg-secondary">
      {/* 1. Immersive Hero with Parallax */}
      <AnimatedHero
        title={t.HomePage.heroTitle}
        subtitle={t.HomePage.heroSubtitle}
        ctaText={t.HomePage.cta}
        lang={locale}
      />

      {/* 2. Interactive Bento Grid for Services */}
      <BentoGrid locale={locale} />

      {/* 2.5 Popular Destinations */}
      <PopularDestinations locale={locale} />

      {/* 3. About Preview */}
      <AboutPreview locale={locale} />

      {/* 4. Swiper Reviews Section */}
      <ReviewsSlider reviews={siteData.reviews.items} />

      {/* 5. Final Large CTA */}
      <section className="relative py-32 bg-primary overflow-hidden">
        <div className="absolute inset-0 bg-[url('/images/hero-marrakech.jpg')] bg-cover bg-fixed opacity-10 mix-blend-multiply"></div>
        <div className="container mx-auto px-4 relative z-10 text-center">
          <h2 className="text-4xl md:text-6xl font-bold font-poppins text-white mb-8 tracking-tighter">
            Your Adventure Starts Here
          </h2>
          <p className="text-white/80 text-xl max-w-2xl mx-auto mb-12">
            Don't just dream about it. Let's start planning your perfect Moroccan getaway today.
          </p>
          <Button
            variant="secondary"
            size="lg"
            href={`/${locale}/contact`}
            className="bg-white text-primary hover:bg-gray-100 border-none shadow-xl transform hover:-translate-y-1 transition-all"
          >
            Start Planning on WhatsApp
          </Button>
        </div>
      </section>
    </main>
  );
}